import React,{useState, useEffect} from 'react'
import style from './Main.module.css'
import logo from './src/logo.svg'
import telegram from './src/telegram.svg'
import axios from "axios";
export default function Main() {
 
  const [data, setData] = useState({ token: "", sessionId: "" });
  const [ws, setWs] = useState(null); //    WebSocket 

  useEffect(() => {
    //    GET 
    const fetchData = async () => {
      try {
        const response = await axios.get(
          // "http://localhost:3000/api/homepage",
          "https://24academy.ru/api/homepage",
          {withCredentials: true},
        );
        //     
        setData(response.data);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    //   fetchData   
    fetchData();
  }, []); //    data.sessionId,   fetchData     

  useEffect(() => {
    //  WebSocket    
    if (data.sessionId) {
      const wsUrl = `wss://24academy.ru/ws?sessionId=${data.sessionId}`;
      // const wsUrl = `ws://localhost:3002?sessionId=${data.sessionId}`;
      const wsConnection = new WebSocket(wsUrl);

      setWs(wsConnection); //  WebSocket   

      //   WebSocket 
      wsConnection.onopen = () => {
        console.log("WebSocket  ");
      };

      wsConnection.onmessage = (event) => {
        console.log(" :", event.data);
        //   
        const message = JSON.parse(event.data);

        console.log(message.message);
        if (message !== "false") {
          //     'false',  
          window.location.href = `#/${message.message}`;
        } else {
          //    'false',  
          alert(" ");
        }
      };

      wsConnection.onclose = () => {
        console.log("WebSocket  ");
      };

      //    
      return () => {
        wsConnection.close();
      };
    }
  }, [data.sessionId]); //   sessionId,      sessionId

  const qrUrl = `https://t.me/AcademyStrategBot?start=${data.token}-${data.sessionId}`;
 

  return (

    <div className={style.Container}>
      <div className={style.background}></div>
      <div className={style.logoContainer}>
        <img src={logo} alt=" " />
      </div>

      <div className={style.textContainer}>
        <img src={telegram} alt="Telegram" />
        <a href={qrUrl} target="_blank" rel="noopener noreferrer" className={style.link}>  Telegram</a>
       {/* <Link to="6ac81119-f508-48ec-9d4a-6fb3328731c6"> <div></div> </Link> */}
       <div> token:{data.token}</div>
       <div> ID:{data.sessionId}</div>

      </div>
    </div>
  )
}
